.list {
    padding: 5px;
    width: 80vw;
}

.listItem {
    display: flex;
    flex-direction: row;
    border-radius: 5px;
    background-color: #98D7C2;
    padding: 10px;
    margin: 35px;
}

.formFrame {
    width: 80vw;
    border-radius: 15px;
    background-color: #98D7C2;
    padding: 15px;
}

.myForm {
    display: flex;
    flex-wrap: wrap;
    padding: 10px;
    margin: 35px;
}

input {
    flex-grow: 1;
    padding: 15px;
    margin: 15px;
    border-radius: 5px;
}

.inputAdd {
    height: 50px;
    min-width: 100px;
    flex-grow: 1;
    padding: 15px;
    margin: 15px;
}

.inputAddSol {
    width: 100%;
    margin: 15px;
}

.btnSubmit {
    background-color: #29A0B1;
    height: 50px;
    color: #fff;
    border:none;
    border-radius: 5px;
    margin:0 15px;
}

.showcase {
    display: flex;
    width: 100%;
}

.listItem ul {
    list-style-type: none;
}

.card {
    flex-grow: 1;
}

.card p {
    margin-left: 15px;
    margin-right: 15px;
}

.identifiers {
    grid-area: identifiers;
    background-color: #DDFFE7;
    padding: 10px;
    display: flex;
    max-height: 35px;
    border: 1px solid #167D7F;
    border-radius: 5px;
    max-width: 164px;
}

.name {
    background-color: #DDFFE7;
    padding: 0 0 25px;
    font-size: 1.5vw;
    min-height: 85px;
}

.description {
    background-color: #DDFFE7;
    padding: 0 0 25px;
    min-height: 85px;
    font-size: 1.1vw;
}

.images {
    background-color: #DDFFE7;
    min-height: 85px;
    padding: 0 0 25px;
}

.solution {
    background-color: #DDFFE7;
    min-height: 85px;
    padding: 0 0 25px;
    font-size: 1vw;
    max-width: 400px;
}

.frame {
    margin: 0 10px;
}

.frame h5 {
    margin: 0;
}

.frame p {
    margin: 0;
}

.titleCard {
    font-size: 0.8vw;
    background-color: #167D7F;
    padding: 3px;
    color: #fff;
}

.cardImage {
    max-width: 250px;
    max-height: 250px;
}

.frameAddImages {
    display: flex;
    flex-direction: column;
    border: 1px solid black;
    border-radius: 5px;
    width: 100%;
    margin:0 15px;
}

.inputFile {
    padding: 5px;
    margin: 5px;
}

.containerImgs {
    border: 5px solid rgb(177, 170, 170);
    border-radius: 5px;
    min-height: 120px;
    margin: 0 15px 15px;
}

.newCardImgs {
    border: 2px solid black;
}

.newCardImgs:hover {
    border: 2px solid white;
}

#updateForm {
    display: flex;
}

.menu ul:not(.sub) {
    padding: 0px;
    margin: 10px;
}

.sub {
    padding: 0px;
}

.fullImageFrame {
    display: none;
}

.fullImage {
    position: fixed;
    width: 100vw;
    height: 100vh;
    background-color: black;
    opacity: 0.5;
    top: 0;
    right: 0;
}

.show {
    display: flex !important;
}

.close {
    width: 50px;
    height: 50px;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
}